import React from "react";
import profileImage from './img/ian-dooley-TLD6iCOlyb0-unsplash.jpg';
import profileImage1 from  './img/adam-bartoszewicz-lNFfYtrbkRM-unsplash.jpg';
import profileImage2 from './img/penguinuhh-BahFTW-j1v4-unsplash.jpg';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

// This site has 3 pages, all of which are rendered
// dynamically in the browser (not server rendered).
//
// Although the page does not ever refresh, notice how
// React Router keeps the URL up to date as you navigate
// through the site. This preserves the browser history,
// making sure things like the back button and bookmarks
// work properly.

export default function BasicExample() {
  return (
    <Router>
      <div>
        <ul>
          <li>
            <Link to="/">Salad</Link>
          </li>
          <li>
            <Link to="/about">Scream</Link>
          </li>
          <li>
            <Link to="/dashboard">Dashboard</Link>
          </li>
        </ul>

        <hr />

        {/*
          A <Switch> looks through all its children <Route>
          elements and renders the first one whose path
          matches the current URL. Use a <Switch> any time
          you have multiple routes, but you want only one
          of them to render at a time
        */}
        <Switch>
          <Route exact path="/">
            <Home />
          </Route>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/dashboard">
            <Dashboard />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

// You can think of these components as "pages"
// in your app.

function Home() {
  return (
    <div><center><h1>Delicious tomato nectarine salad! </h1>
<img src={profileImage1} alt="cafe" width="400" height="600"/>
</center></div>
  );
}

function About() {
  return (
    <div><center><h1>I scream You Scream</h1>
      <img src={profileImage}alt="cafe" width="400" height="600"/>
      </center></div>
  );
}

function Dashboard() {
  return (
    <div><center>
      <img src={profileImage2} alt="cafe" width="400" height="600" />
    </center></div>
  );
}
